// frontend/src/config.js
// API Configuration for your backend

// Compute API base at runtime.
// - If `REACT_APP_API_URL` is explicitly set (and not a legacy LAN value), use it.
// - Otherwise, use relative API paths so requests go to the current origin (recommended when
//   the frontend is served by the backend or through a tunnel like ngrok).
const envUrl = process.env.REACT_APP_API_URL || '';
const isLegacy = envUrl && (envUrl.includes('192.168.54.') || envUrl.includes('backend.loca.lt'));
const API_BASE = envUrl && !isLegacy ? envUrl.replace(/\/+$/,'') : '';

// Backwards-compatible default name used across the codebase
const API_URL = API_BASE;

const join = (base, path) => {
  if (!base) return path.startsWith('/') ? path : `/${path}`;
  return `${base.replace(/\/+$/,'')}/${path.replace(/^\/+/, '')}`;
};

export const API_ENDPOINTS = {
  // Auth endpoints (mounted under /api/* on the backend)
  SIGNUP: join(API_BASE, '/api/signup'),
  LOGIN: join(API_BASE, '/api/login'),

  // Prediction endpoint - backend exposes this under /api/predict
  PREDICT: join(API_BASE, '/api/predict'),

  // Health check
  HEALTH: join(API_BASE, '/api/health'),
};

// Helper function for making API calls
export const apiCall = async (endpoint, options = {}) => {
  try {
    const response = await fetch(endpoint, {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return await response.json();
  } catch (error) {
    console.error('API call failed:', error);
    throw error;
  }
};

// Helper for image uploads
export const uploadImage = async (file, additionalData = {}) => {
  try {
    const formData = new FormData();
    formData.append('image', file);
    
    // Add any additional form data
    Object.keys(additionalData).forEach(key => {
      formData.append(key, additionalData[key]);
    });

    const response = await fetch(API_ENDPOINTS.PREDICT, {
      method: 'POST',
      body: formData,
      // Don't set Content-Type header - browser will set it with boundary
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    return await response.json();
  } catch (error) {
    console.error('Image upload failed:', error);
    throw error;
  }
};

export default API_URL;