import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import API_URL from '../config';

const computeApiBase = () => {
  const envUrl = process.env.REACT_APP_API_URL || API_URL || '';
  if (envUrl && !envUrl.includes('192.168.54.') && !envUrl.includes('backend.loca.lt')) {
    return envUrl;
  }
  if (typeof window !== 'undefined' && window.location && window.location.origin) {
    const origin = window.location.origin;
    if (origin && !origin.includes('localhost')) return origin;
  }
  return 'http://localhost:5000';
};

const API_BASE_URL = computeApiBase();

function Login() {
  const [isLogin, setIsLogin] = useState(true);
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    name: ''
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    // Clear error when user types
    if (error) setError('');
  };

  // Helper: try a list of candidate base URLs until one returns JSON
  const tryPostCandidates = async (endpoint, body) => {
    const candidates = [];

    // 1) Explicit env/config base
    if (API_BASE_URL) candidates.push(API_BASE_URL.replace(/\/+$/, ''));

    // 2) current origin (useful when backend and frontend are same origin)
    try {
      if (typeof window !== 'undefined' && window.location && window.location.origin) {
        candidates.push(window.location.origin);
      }
    } catch (e) {}

    // 3) same host but port 5000 (common backend port)
    try {
      if (typeof window !== 'undefined' && window.location && window.location.hostname) {
        const proto = window.location.protocol || 'http:';
        const hostWithPort = `${proto}//${window.location.hostname}:5000`;
        candidates.push(hostWithPort);
      }
    } catch (e) {}

    // 4) localhost fallback
    candidates.push('http://localhost:5000');

    const tried = new Set();
    for (const base of candidates) {
      if (!base) continue;
      const baseNormalized = base.replace(/\/+$/, '');
      if (tried.has(baseNormalized)) continue;
      tried.add(baseNormalized);

      const url = `${baseNormalized.replace(/\/$/, '')}${endpoint.startsWith('/') ? endpoint : '/' + endpoint}`;
      console.log(`Attempting login POST to candidate: ${url}`);

      try {
        const resp = await fetch(url, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(body),
        });

        const ct = resp.headers.get('content-type') || '';

        if (!resp.ok) {
          // if server returned HTML (e.g., frontend index), treat as non-JSON and continue
          if (!ct.includes('application/json')) {
            const txt = await resp.text().catch(() => '');
            console.warn(`Candidate ${baseNormalized} returned non-JSON (status ${resp.status}).`);
            continue; // try next candidate
          }

          const errBody = await resp.json().catch(() => ({}));
          throw new Error(errBody.message || `HTTP ${resp.status}`);
        }

        if (!ct.includes('application/json')) {
          const text = await resp.text().catch(() => '');
          console.warn('Received non-JSON response:', text.substring(0, 200));
          continue; // try next
        }

        const data = await resp.json();
        return { data, base: baseNormalized };
      } catch (err) {
        console.warn(`Request to ${baseNormalized} failed:`, err.message || err);
        // try next
      }
    }

    throw new Error('All backend candidates failed or returned non-JSON responses. Check backend and REACT_APP_API_URL.');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const endpoint = isLogin ? '/api/auth/login' : '/api/auth/register';
      const result = await tryPostCandidates(endpoint, formData);

      const data = result.data;
      console.log(' Response received from', result.base, data);

      if (data.token) localStorage.setItem('token', data.token);
      if (data.userId || data.user?.id) localStorage.setItem('userId', data.userId || data.user.id);
      if (data.name || data.user?.name) localStorage.setItem('userName', data.name || data.user.name);

      navigate('/form');
    } catch (err) {
      console.error(' Auth error:', err);
      setError(err.message || 'Authentication failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-white flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        <div className="bg-white rounded-xl shadow-md p-8 border border-gray-100">
          {/* Header */}
          <h1 className="text-3xl font-bold text-center text-gray-800 mb-2">
            {isLogin ? 'Login' : 'Sign Up'}
          </h1>
          <p className="text-center text-gray-600 mb-6">
            {isLogin ? 'Welcome back to Skin Disease Detection' : 'Create your account'}
          </p>

          {/* Error Message */}
          {error && (
            <div className="mb-6 p-4 bg-red-50 border-l-4 border-red-500 rounded-r-lg">
              <p className="text-red-700 text-sm font-medium">{error}</p>
            </div>
          )}

          {/* Form */}
          <form onSubmit={handleSubmit} className="space-y-4" autoComplete="off">
            {/* Hidden dummy fields to discourage browser autofill */}
            <input type="text" name="fakeusernameremembered" autoComplete="off" style={{ display: 'none' }} />
            <input type="password" name="fakepasswordremembered" autoComplete="new-password" style={{ display: 'none' }} />
            {/* Name Field (only for signup) */}
            {!isLogin && (
              <div>
                <label className="block text-sm font-semibold text-gray-700 mb-2">Full Name</label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  placeholder="John Doe"
                  className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                  required={!isLogin}
                />
              </div>
            )}

            {/* Email Field */}
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">Email</label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                placeholder="your@email.com"
                autoComplete="off"
                className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                required
              />
            </div>

            {/* Password Field */}
            <div>
              <label className="block text-sm font-semibold text-gray-700 mb-2">Password</label>
              <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                placeholder=""
                autoComplete="new-password"
                className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all"
                required
              />
            </div>

            {/* Submit Button */}
            <button
              type="submit"
              disabled={loading}
              className="w-full py-3 bg-gradient-to-r from-blue-500 to-indigo-600 text-white rounded-lg hover:from-blue-600 hover:to-indigo-700 disabled:from-gray-400 disabled:to-gray-500 disabled:cursor-not-allowed font-bold text-lg transition-all"
            >
              {loading ? (
                <span className="flex items-center justify-center gap-2">
                  <svg className="animate-spin h-5 w-5" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  {isLogin ? 'Logging in...' : 'Creating account...'}
                </span>
              ) : (
                <span>{isLogin ? 'Login' : 'Sign Up'}</span>
              )}
            </button>
          </form>

          {/* Toggle Login/Signup */}
          <div className="mt-6 text-center">
            <p className="text-gray-700">
              {isLogin ? "Don't have an account? " : 'Already have an account? '}
              <button
                type="button"
                onClick={() => {
                  setIsLogin(!isLogin);
                  setError('');
                  setFormData({ email: '', password: '', name: '' });
                }}
                className="text-indigo-600 hover:text-indigo-700 font-semibold"
              >
                {isLogin ? 'Sign Up' : 'Login'}
              </button>
            </p>
          </div>

          {/* Debug Info */}
          <div className="mt-6 p-3 bg-gray-50 rounded-lg text-sm text-gray-700">
            <p className="font-mono">Debug: Connecting to <strong>{API_BASE_URL || 'relative origin (no REACT_APP_API_URL set)'}</strong></p>
            <p className="mt-2 text-xs text-gray-500">If this shows your frontend/ngrok URL instead of the backend ngrok URL, set `REACT_APP_API_URL` to the backend URL and restart the frontend.</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;